<!--
*****
***** Readme
***** 
***** getValue() 
***** setTaskText(text)  text
*****
 -->
<template lang="html">
  <div class="c-timer">

    <!--   -->
    <div v-show="currentPage === 'main'">
      <!--  -->
      <v-panel>
        <div slot="body">
          <v-timepicker
          ref="timepicker"
          :hour="_initTime.hour"
          :min="_initTime.min"
          @change='_pickerHandle'
          :rotate_effect="false"
          ></v-timepicker>
        </div>
      </v-panel>

      <!--  -->
      <v-panel>

        <a slot="body" class="u-relative c-panel-body row-1 u-cross-center" @click="_emitJump('repeat')">
          <div class="title-not-flex"></div>
          <div class="c-timer-content control-field">
            {{ this._repeat }}
            <span class="icon icon-pull-right"></span>
        </a>

      </v-panel>

      <!-- simple task  -->
      <!--  -->
      <template v-if="options.mainpage.simple">
        <template v-if="!options.mainpage.simple.hideDefault">

          <v-panel>
            <div slot="header" class="c-panel-header row-1 u-cross-center">
              {{ options.mainpage.simple.title }}
            </div>
            <div slot="body" class="c-panel-body u-without-padding">
              <v-modes v-model="switch_active_id"
              :numberal="2" :items="switch_data"
              ></v-modes>
            </div>
          </v-panel>

        </template>

        <slot name="simple"></slot>
      </template>

      <!--   -->
      <template v-else>
        <v-panel>
          <a slot="body" class="u-relative c-panel-body row-1 u-cross-center" @click="_emitJump('task')" >
            <div class="title-not-flex"></div>
            <div class="c-timer-content control-field">
              {{ this.taskText }} <span class="icon icon-pull-right"></span>
            </div>
          </a>
        </v-panel>
      </template>

      <!--  -->
      <v-panel>
        <a slot="body" class="u-relative c-panel-body row-1 u-cross-center" @click="_emitJump('name')">

          <div class="title-not-flex"></div>
          <div class="c-timer-content control-field">
            {{ task_name }}
            <span class="icon icon-pull-right"></span>

          </div>
        </a>
      </v-panel>

      <!--  -->
      <v-panel>
        <a slot="body" class="u-relative c-panel-body row-1 u-cross-center" @click="_emitJump('notice')">
          <div class="title-not-flex"></div>
          <div class="c-timer-content">
            {{ this._notice }} <span class="icon icon-pull-right"></span>
          </div>
        </a>
      </v-panel>

      <!--  -->
      <div v-if="_showDelect" class="c-panel">
        <div ref="btn_delete" class="c-panel-body row-1 c-timer-center u-cross-center ">
          
        </div>
      </div>

      <!--   -->
      <v-dialog ref="dialog_delete" :showDialog="showDialog" :options="dialogOptions"></v-dialog>
    </div>

    <!--   -->
    <div v-show="currentPage === 'repeat'">
      <v-panel>
        <div slot="body" class="c-panel-body row-1 u-cross-center">
          <div class="">
            
          </div>
          <div class="">
            <v-switch v-model="repeat_switch"></v-switch>
          </div>
        </div>
        <div v-show="repeat_switch" slot="body" class="c-panel-body row-5 u-without-padding c-timer-border-top">
          <v-dayspicker ref="dayspicker" :days="days" @change="_dayspickerHandle">
          </v-dayspicker>
        </div>
      </v-panel>
    </div>

    <!--   -->
    <!--   taskpage -->
    <template v-if="!options.mainpage.simple && options.taskpage">
      <div v-show="currentPage === 'task'">
        <v-timer-task ref="task" :options="options.taskpage" @change="_taskHandle">
          <!--  -->
          <slot name="task" @change="slotHandle"></slot>
        </v-timer-task>
      </div>
    </template>


    <!--  -->
    <div v-show="currentPage === 'name'">
      <v-panel>
        <div slot="body" class="c-panel-body row-1 u-cross-center">
          <v-input class="c-timer-input" v-model="task_name" placeholder=""></v-input>
        </div>
      </v-panel>
    </div>

    <!--   -->
    <div v-show="currentPage === 'notice'">
      <v-panel>
        <div slot="body" class="c-panel-body u-without-padding u-border m-border">
          <v-checkboard :items="checkboardItems" v-model="notice"></v-checkboard>
        </div>
      </v-panel>
    </div>

  </div>
</template>

<script>
import units from '../units/default';

export default {
  name: 'v-timer',

  data() {
    return {
      currentPage: 'main',
      days: [], // ,
      taskValue: [], // taskpage
      task_name: this.options.mainpage.task_name, // 
      notice: this.options.mainpage.pmg_setting * 1, // pmg_setting
      time: this._initTime, // 
      taskText: this._task, //  
      showDialog: false, // 
      dialogOptions: {
        title: '',
        // description: '',
        isModal: true,
        buttons: [
          {
            text: '',
          },
          {
            text: '',
          },
        ],
      },

      switch_active_id: this.options.mainpage.simple.status ? 1 : 0,
      switch_data: [{
        text: '',
        id: 0,
      }, {
        text: '',
        id: 1,
      }],

      checkboardItems: [{
        text: '',
        id: -1,
      }, {
        text: '',
        id: 0,
      }, {
        text: '',
        id: 1,
      }],

      repeat_switch: false,
    };
  },

  props: {
    options: {
      type: Object,
      required: true,
    },
  },


  watch: {
    _task(val) {
      this.taskText = val;
    },

    options(val, oldVal) {
      // timepickerupdatepicker
      if (val.mainpage.task_time_express === oldVal.mainpage.task_time_express) {
        this.$refs.timepicker.update();
      }

      // dom
      this.$nextTick(() => {
        this._created();
        this._mounted();
        this.switch_active_id = this.options.mainpage.simple.status ? 1 : 0;
        this.task_name = this.options.mainpage.task_name;
        this.notice = this.options.mainpage.pmg_setting * 1;
      });
    },
  },

  computed: {
    _repeat() {
      if (!this.repeat_switch || this.days.length === 0) {
        return '';
      }

      const str = units.weekArrToStr(this.days);
      return str;
    },

    _notice() {
      const n = this.notice;

      switch (n) {
        case -1:
          return '';
        case 0:
          return '';
        case 1:
          return '';
        default:
          return '';
      }
    },

    _initTime() {
      let time = {};

      // ``
      if (!this.options.mainpage.task_time_express) {
        time = {
          min: new Date().getMinutes(),
          hour: new Date().getHours(),
        };

      // 
      } else {

        // 
        const min = this.options.mainpage.task_time_express.split('_')[0] * 1;
        const hour = this.options.mainpage.task_time_express.split('_')[1] * 1;

        time = {
          hour,
          min,
        };
      }
      return time;
    },

    _task() {
      // taskValue;
      if (this.taskValue.length === 0) return '';
      let arr = [];

      // onOff
      if (this.taskValue[0].name === 'onOff' && !this.taskValue[0].value) {
        return '';
      }

      // task
      arr = this.taskValue.map(val => val.text);

      return arr.join('/');
    },

    _showDelect() {
      if (this.options.mainpage.show_delete) return true;
      return false;
    },
  },

  created() {
    this._created(this);
  },

  mounted() {
    this._mounted();
  },

  methods: {
    // 
    getValue() {
      let taskTimeExpress = '';

      // value
      if (this.repeat_switch && this.days.length > 0) {
        // 
        taskTimeExpress = units.taskTimeExpress(this.time[1], this.time[0], '*', '*', this.days);
      } else {

        // 
        const nowdate = this.getNowDate();

        // 
        if ((this.time[0] * 60) + this.time[1]
        <= (nowdate.hour * 60) + nowdate.min) {

          taskTimeExpress = units.taskTimeExpress(this.time[1], this.time[0], nowdate.date + 1, nowdate.month, '*', nowdate.year);
        } else {

          // 
          taskTimeExpress = units.taskTimeExpress(this.time[1], this.time[0], nowdate.date, nowdate.month, '*', nowdate.year);
        }
      }

      // 
      const value = {
        task_time_express: taskTimeExpress,
        notice: this.notice,
        task_name: this.task_name,
      };

      // value
      if (this.options.mainpage.simple) {
        Object.assign(value, {
          simple_switch: this.switch_active_id === 1,
        });
      }

      // value
      if (this.taskValue.length > 0) {
        Object.assign(value, {
          taskList: this.taskValue,
        });
      }
      return value;
    },

    update() {
      this.$forceUpdate();
    },

    // 
    setTaskText(val) {
      this.taskText = val;
    },

    // 
    getNowDate() {
      const date = new Date();
      return {
        hour: date.getHours(),
        min: date.getMinutes(),
        date: date.getDate(),
        month: date.getMonth() + 1,
        year: date.getFullYear(),
      };
    },

    jump(val) {
      // 
      if (this._repeat === '') {
        this.repeat_switch = false;
      }

      this.currentPage = val;
    },

    _emitJump(val) {
      this.$emit('jump', val);
    },

    _dayspickerHandle(val) {
      this.days = val;
    },

    _pickerHandle(val) {
      this.time = val;
    },

    _taskHandle(val) {
      this.taskValue = val;
    },

    _created() {
      console.log(JSON.stringify(this.options));

      // 
      // 
      if (this.options.mainpage.task_time_express) {

        const week = units.arrayTaskTimeExpress(this.options.mainpage.task_time_express);

        // 
        if (week.length > 0) {
          this.days = week;
          this.repeat_switch = true;

        // 
        } else {
          this.repeat_switch = false;
        }

      // 
      } else {
        this.repeat_switch = false;
      }
    },

    _mounted() {
      if (this.options.mainpage.show_delete) {
        this.$refs.btn_delete.addEventListener('click', () => {
          this.showDialog = true;
        });
      }

      // 
      this.$refs.dialog_delete.$on('defaultClick', () => {
        this.showDialog = false;
      });

      // 
      this.$refs.dialog_delete.$on('primaryClick', () => {
        this.showDialog = false;
        this.$emit('delete');
      });
    },
  },
};

</script>

<style lang="css">
  @import '../styles/default-theme/variables.css';

  .c-timer{
    a{
      text-decoration: none;
      color: #2c3e50;
      user-select: none;

      -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
    }
    .u-relative{
      position: relative;

      .c-timer-content{
        padding-right: 12px;
      }

      .icon{
        width: 16px;
        height: 16px;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0.06rem;
        margin: auto;
        transform: translate(0, -4%);
      }
    }

    .c-timer-content{
      color: $gray-light;
    }
    .c-timer-center{
      justify-content: center;
    }

    .c-timer-border-top{
      border-top: 1px solid #e5e5e5;
    }

    .c-dayspicker-quick{
      padding: 0.12rem;
    }

    .c-dayspicker-body{
      padding: 0 0.12rem;
    }

    .c-timer-input{
      width: 100%;
    }
  }

  .m-border{
    border: 1px solid $gray-lighter
  }
</style>



