<!--
*******
******* Tips
*******  change . toggle, turnOn  turnOff 
*******  change 
******* longTap === true  1000ms  150ms  change 
*******
*******  toggle 
*******  init_status   status
*******  statusupdate(boolean) 
*******
 -->

<template>
  <div
  :data-value="`${status}`"
  :class="[ btnCls,
            `c-btn-${size}`,
            disabled ? disabledCls : '',
            hover ? hoverCls : '',
            status ? activeCls : '',
          ]"
  :text="text"
  :type="type"
  @touchstart="touchstartHandle"
  @touchend="touchendHandle"
  @touchcancel="touchcancelHandle"
  >
    <span v-if="icon !== '' " :class="['c-btn-icon', `icon-${icon}`]"></span>
    <span v-if="text !== '' " >{{ text }}</span>
  </div>
</template>

<script>

let longTapFlagInterval = null;
let longTapInterval = null;

export default {
  name: 'v-button',

  data() {
    return {
      hover: false,
      longTapFlag: false,
      status: this.init_status,
    };
  },

  props: {
    // optinos
    options: {
      type: Object,
      required: false,
      default() {
        return {};
      },
    },
    // 
    type: {
      type: String,
      // required: false,
      default: 'click',
      validator(value) {
        const list = ['toggle', 'click'];
        if (list.indexOf(value) < 0) {
          console.error("[v-button] prop 'type' error: yon pass an invalid value, please pass a value in 'toggle' 'click'");
          return false;
        }
        return true;
      },
    },
    // 
    size: {
      type: String,
      // required: false,
      default: 'base',
      validator(value) {
        const list = ['sm', 'base', 'lg'];
        if (list.indexOf(value) < 0) {
          console.error("[v-button] prop 'size' error: yon pass an invalid value, please pass a value in 'sm' 'base' 'lg'");
          return false;
        }
        return true;
      },
    },
    // 
    text: {
      type: String,
      required: false,
      default: '',
    },
    // icon
    icon: {
      type: String,
      required: false,
      default: '',
    },
    // 
    disabled: {
      type: Boolean,
      reuqired: false,
      default: false,
    },
    // 
    btnCls: {
      type: String,
      required: false,
      default: 'c-btn',
    },
    // 
    hoverCls: {
      type: String,
      required: false,
      default: 'c-btn-hover',
    },
    // disabled
    disabledCls: {
      type: String,
      required: false,
      default: 'c-btn-disabled',
    },
    //  toggle
    activeCls: {
      type: String,
      required: false,
      default: 'c-btn-on',
    },
    //  toggle
    // activeHoverCls: {
    //   type: String,
    //   required: false,
    //   default: 'c-btn-on',
    // },
    // 
    longTap: {
      type: Boolean,
      required: false,
      default: false,
    },
    // 
    init_status: {
      type: Boolean,
      required: false,
      default: false,
    },
    // statusdisabled, statusstatus,
    // disableddisabledhold
    hold: {
      type: Boolean,
      required: false,
      default: false,
    },
  },

  watch: {

    longTapFlag(val) {
      if (longTapInterval) {
        window.clearInterval(longTapInterval);
        longTapInterval = null;
      }
      if (val === false) {
        return;
      }

      const that = this;
      longTapInterval = window.setInterval(() => {
        that.$emit('change');
      }, 150);
    },

    // status  watcher  status  change 
    status() {
      this.$emit('change', this.status);

      if (this.status) {
        this.$emit('turnOn', this.status);
      } else {
        this.$emit('turnOff', this.status);
      }
    },
  },

  methods: {
    // updatestatus
    update(boolean) {
      this.status = boolean;
    },

    // 
    stopInterval() {
      window.clearInterval(longTapFlagInterval);
      this.longTapFlag = false;
      longTapFlagInterval = null;
    },

    touchstartHandle() {
      // longTap
      if (this.longTap) {
        longTapFlagInterval = window.setInterval(() => {
          this.longTapFlag = true;
        }, 1000);
      }
      // 
      if (this.disabled) {
        this.hover = false;
        return;
      }
      this.hover = true;
    },

    touchendHandle() {
      // longTap
      if (this.longTap) {
        window.clearInterval(longTapFlagInterval);
        this.longTapFlag = false;
        longTapFlagInterval = null;
      }
      // 
      if (this.disabled) {
        // 
        this.hover = false;
        return;
      }
      // 
      this.hover = false;

      // click statuschange
      if (this.type === 'click') {
        this.$emit('change');
        return;
      }

      // hold === true 
      if (this.hold === true) {
        // this.$emit('change', this.status);
        return;
      }

      this.status = !this.status;
      // status  watcher 
    },

    touchcancelHandle() {
      this.touchendHandle();
      console.log('trigger touchcancel event!');
    },
  },


};
</script>

<style>
  @import '../styles/default-theme/variables.css';
  @import '../styles/mixins.css';


  .c-btn {
    display: flex;
    align-items: center;
    justify-content: center;

    @mixin border;
    border-color: $c-primary;
    border-radius: @width;
    border-color: $c-primary;

    color: $c-primary;
    font-size: $font-size-base;

    @mixin transition;

    span {
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -khtml-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }

    span + span{
      margin-left: 0.05rem;
      margin-bottom: -1px;
    }
  }

  .c-btn-hover{
    background: $btn-hover;

    &.c-btn-on{
      background-color: $c-primary-on;
    }
  }

  .c-btn-on{
    color: $white;
    background-color: $blue;
  }

  .c-btn-base {
    width: $btn-width-base;
    height: $btn-height-base;

    border-radius: @width;
  }

  .c-btn-sm {
    width: $btn-width-sm;
    height: $btn-height-sm;

    border-radius: @width;
  }

  .c-btn-lg {
    width: $btn-width-lg;
    height: $btn-height-lg;

    border-radius: @width;
  }

  #app .c-btn-disabled {
    color: $btn-disabled;
    /*background-color: $btn-disabled;*/
    border-color: $btn-disabled;
  }


  .c-btn-icon{
    font-size: calc($font-size-base + 4px);

    &:before{
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }
  }
</style>



